import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useRifas } from '../contexts/RifasContext';

const UserDashboard = () => {
  const { myRifas, loading, error } = useRifas();
  const [filtro, setFiltro] = useState('todas'); // 'todas', 'activas', 'finalizadas', 'privadas'
  const [busqueda, setBusqueda] = useState(''); // Filtro de bsqueda por texto
  const [currentRifaIndex, setCurrentRifaIndex] = useState(0); // ndice actual del carrusel
  const [touchStartY, setTouchStartY] = useState(0);
  const [touchEndY, setTouchEndY] = useState(0);
  
  // Debug: verificar que rifas llegue correctamente
  console.log('UserDashboard - myRifas del contexto:', myRifas);

  // Filtrar rifas segn el filtro seleccionado y bsqueda
  const rifasFiltradas = myRifas.filter(rifa => {
    // Filtro por estado
    let cumpleFiltro = true;
    switch (filtro) {
      case 'activas':
        cumpleFiltro = rifa.activa === true;
        break;
      case 'finalizadas':
        cumpleFiltro = rifa.activa === false;
        break;
      case 'privadas':
        cumpleFiltro = rifa.es_privada === true;
        break;
      default:
        cumpleFiltro = true;
    }
    
    // Filtro por bsqueda de texto
    if (busqueda.trim()) {
      const terminoBusqueda = busqueda.toLowerCase();
      const cumpleBusqueda = 
        rifa.nombre.toLowerCase().includes(terminoBusqueda) ||
        (rifa.descripcion && rifa.descripcion.toLowerCase().includes(terminoBusqueda)) ||
        rifa.tipo.toLowerCase().includes(terminoBusqueda);
      
      return cumpleFiltro && cumpleBusqueda;
    }
    
    return cumpleFiltro;
  });

  // Calcular estadsticas de las rifas
  const estadisticas = {
    total: myRifas.length,
    activas: myRifas.filter(r => r.activa === true).length,
    finalizadas: myRifas.filter(r => r.activa === false).length,
    privadas: myRifas.filter(r => r.es_privada === true).length,
    totalRecaudado: myRifas.reduce((total, r) => total + (parseFloat(r.precio) * parseInt(r.elementos_vendidos || 0)), 0)
  };

  // Funciones para el scroll container
  const scrollToRifa = (index) => {
    setCurrentRifaIndex(index);
    const container = document.querySelector('.scroll-container');
    if (container) {
      const cardHeight = container.scrollHeight / rifasFiltradas.length;
      container.scrollTo({
        top: index * cardHeight,
        behavior: 'smooth'
      });
    }
  };

  const handleScroll = (e) => {
    const container = e.target;
    const cardHeight = container.scrollHeight / rifasFiltradas.length;
    const scrollTop = container.scrollTop;
    const newIndex = Math.round(scrollTop / cardHeight);
    
    if (newIndex !== currentRifaIndex && newIndex >= 0 && newIndex < rifasFiltradas.length) {
      setCurrentRifaIndex(newIndex);
    }
  };

  // Resetear ndice cuando cambien los filtros
  React.useEffect(() => {
    setCurrentRifaIndex(0);
  }, [filtro, busqueda]);

  // Mostrar estado de carga
  if (loading) {
    return (
      <div className="dashboard-container dashboard">
        <div className="loading-state">
          <div className="loading-spinner"></div>
          <p>Cargando tus rifas...</p>
        </div>
      </div>
    );
  }

  // Mostrar error si existe
  if (error) {
    return (
      <div className="dashboard-container dashboard">
        <div className="error-state">
          <h2> Error</h2>
          <p>{error}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="dashboard-container dashboard">
      <div className="dashboard-header">
        <h1> Mi Dashboard</h1>
        <p>Gestiona tus rifas creadas</p>
      </div>

      <div className="dashboard-stats">
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-number">{estadisticas.total}</span>
            <span className="stat-label">Total Rifas</span>
          </div>
        </div>
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-number">{estadisticas.activas}</span>
            <span className="stat-label">Activas</span>
          </div>
        </div>
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-number">{estadisticas.finalizadas}</span>
            <span className="stat-label">Finalizadas</span>
          </div>
        </div>
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-number">{estadisticas.privadas}</span>
            <span className="stat-label">Privadas</span>
          </div>
        </div>
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-number">${estadisticas.totalRecaudado}</span>
            <span className="stat-label">Total Recaudado</span>
          </div>
        </div>
      </div>

      <div className="dashboard-controls">
        {/* Campo de bsqueda */}
        <div className="search-container">
          <div className="search-input-wrapper">
            <input
              type="text"
              placeholder=" Buscar rifas por nombre, descripcin o tipo..."
              value={busqueda}
              onChange={(e) => setBusqueda(e.target.value)}
              className="search-input"
            />
            {busqueda && (
              <button 
                className="clear-search-btn"
                onClick={() => setBusqueda('')}
                title="Limpiar bsqueda"
              >
                
              </button>
            )}
          </div>
        </div>

        {/* Filtros por estado */}
        <div className="filtros-dashboard">
          <button 
            className={`filtro-dashboard-btn ${filtro === 'todas' ? 'activo' : ''}`}
            onClick={() => setFiltro('todas')}
          >
            Todas ({estadisticas.total})
          </button>
          <button 
            className={`filtro-dashboard-btn ${filtro === 'activas' ? 'activo' : ''}`}
            onClick={() => setFiltro('activas')}
          >
            Activas ({estadisticas.activas})
          </button>
          <button 
            className={`filtro-dashboard-btn ${filtro === 'finalizadas' ? 'activo' : ''}`}
            onClick={() => setFiltro('finalizadas')}
          >
            Finalizadas ({estadisticas.finalizadas})
          </button>
          <button 
            className={`filtro-dashboard-btn ${filtro === 'privadas' ? 'activo' : ''}`}
            onClick={() => setFiltro('privadas')}
          >
            Privadas ({estadisticas.privadas})
          </button>
        </div>
      </div>

      {/* Tabla de rifas - Solo Desktop */}
      <div className="table-container desktop-only">
        {rifasFiltradas.length === 0 ? (
          <div className="no-rifas">
            <h3>No tienes rifas creadas</h3>
            <p>Crea tu primera rifa y comienza a recaudar!</p>
            <Link to="/gestionar" className="btn-primary">Crear Rifa</Link>
          </div>
        ) : (
          <table className="rifas-table">
            <thead>
              <tr>
                <th>Rifa</th>
                <th>Tipo</th>
                <th>Precio</th>
                <th>Progreso</th>
                <th>Participantes</th>
                <th>Estado</th>
                <th>Fecha Fin</th>
                <th>Acciones</th>
              </tr>
            </thead>
            <tbody>
              {rifasFiltradas.map(rifa => (
                <tr key={rifa.id}>
                  <td>
                    <div className="rifa-name">
                      <strong>{rifa.nombre}</strong>
                      {rifa.descripcion && (
                        <small className="rifa-description">{rifa.descripcion}</small>
                      )}
                    </div>
                  </td>
                  <td>
                    <span className="rifa-type">
                      {rifa.tipo === 'numeros' ? 'Nmeros' :
                       rifa.tipo === 'abecedario' ? 'Abecedario' :
                       rifa.tipo === 'baraja' ? 'Baraja' :
                       rifa.tipo === 'colores' ? 'Colores' :
                       rifa.tipo === 'equipos' ? 'Equipos' : rifa.tipo}
                    </span>
                  </td>
                  <td>
                    <span className="rifa-price">${rifa.precio}</span>
                  </td>
                  <td>
                    <div className="progress-info">
                      <span className="progress-text">
                        {rifa.elementos_vendidos || 0}/{rifa.cantidad_elementos}
                      </span>
                      <div className="progress-bar">
                        <div 
                          className="progress-fill"
                          style={{
                            width: `${((rifa.elementos_vendidos || 0) / rifa.cantidad_elementos) * 100}%`
                          }}
                        ></div>
                      </div>
                    </div>
                  </td>
                  <td>
                    <span className="participants-count">
                      {rifa.total_participantes || 0}
                    </span>
                  </td>
                  <td>
                    <span className={`status-badge ${rifa.activa ? 'active' : 'finished'}`}>
                      {rifa.activa ? ' Activa' : ' Finalizada'}
                      {rifa.es_privada && ' '}
                    </span>
                  </td>
                  <td>
                    <span className="end-date">
                      {new Date(rifa.fecha_fin).toLocaleDateString('es-MX')}
                    </span>
                  </td>
                  <td>
                    <div className="action-buttons">
                      <Link to={`/gestionar/${rifa.id}`} className="btn-manage">
                        Gestionar
                      </Link>
                      <Link to={`/public/${rifa.id}`} className="btn-view">
                        Ver Pblico
                      </Link>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>

      {/* Scroll Container de Rifas - Solo Mvil */}
      <div className="rifas-scroll-container mobile-only">
        {rifasFiltradas.length === 0 ? (
          <div className="no-rifas">
            <h3>No tienes rifas creadas</h3>
            <p>Crea tu primera rifa y comienza a recaudar!</p>
            <Link to="/gestionar" className="btn-primary">Crear Rifa</Link>
          </div>
        ) : (
          <div className="scroll-wrapper">
            {/* Indicadores de navegacin */}
            <div className="scroll-indicators">
              <span className="scroll-counter">
                {currentRifaIndex + 1} de {rifasFiltradas.length}
              </span>
              <div className="scroll-dots">
                {rifasFiltradas.map((_, index) => (
                  <button
                    key={index}
                    className={`scroll-dot ${index === currentRifaIndex ? 'active' : ''}`}
                    onClick={() => scrollToRifa(index)}
                  />
                ))}
              </div>
            </div>

            {/* Contenedor de scroll con 3 cards visibles */}
            <div 
              className="scroll-container"
              onScroll={handleScroll}
            >
              {rifasFiltradas.map((rifa, index) => (
                <div 
                  key={rifa.id} 
                  className={`aurela-card-premium scroll-card ${index === currentRifaIndex ? 'active' : ''}`}
                >
                  {/* Header en una sola lnea */}
                  <div className="aurela-card-header">
                    <h3 className="aurela-card-title">{rifa.nombre}</h3>
                    <div className="aurela-card-badges">
                      <span className={`aurela-status-badge ${rifa.activa ? 'active' : 'finished'}`}>
                        {rifa.activa ? ' Activa' : ' Finalizada'}
                      </span>
                      {rifa.es_privada && <span className="aurela-privacy-badge"> Privada</span>}
                    </div>
                    <div className="aurela-card-price">
                      <span className="aurela-price-symbol">$</span>
                      <span className="aurela-price-amount">{rifa.precio}</span>
                    </div>
                  </div>

                  {/* Informacin principal - Layout compacto */}
                  <div className="aurela-card-content">
                    <div className="aurela-info-grid-compact">
                      <div className="aurela-info-item-compact">
                        <div className="aurela-info-icon-compact"></div>
                        <div className="aurela-info-content-compact">
                          <span className="aurela-info-label-compact">TIPO</span>
                          <span className="aurela-info-value-compact">
                            {rifa.tipo === 'numeros' ? 'Nmeros' :
                             rifa.tipo === 'abecedario' ? 'Abecedario' :
                             rifa.tipo === 'baraja' ? 'Baraja' :
                             rifa.tipo === 'colores' ? 'Colores' :
                             rifa.tipo === 'equipos' ? 'Equipos' : rifa.tipo}
                          </span>
                        </div>
                      </div>

                      <div className="aurela-info-item-compact">
                        <div className="aurela-info-icon-compact"></div>
                        <div className="aurela-info-content-compact">
                          <span className="aurela-info-label-compact">PROGRESO</span>
                          <span className="aurela-info-value-compact">{rifa.elementos_vendidos || 0}/{rifa.cantidad_elementos}</span>
                        </div>
                      </div>

                      <div className="aurela-info-item-compact">
                        <div className="aurela-info-icon-compact"></div>
                        <div className="aurela-info-content-compact">
                          <span className="aurela-info-label-compact">PARTICIPANTES</span>
                          <span className="aurela-info-value-compact">{rifa.total_participantes || 0}</span>
                        </div>
                      </div>

                      <div className="aurela-info-item-compact">
                        <div className="aurela-info-icon-compact"></div>
                        <div className="aurela-info-content-compact">
                          <span className="aurela-info-label-compact">FINALIZA</span>
                          <span className="aurela-info-value-compact">{new Date(rifa.fecha_fin).toLocaleDateString('es-MX')}</span>
                        </div>
                      </div>
                    </div>

                    {/* Barra de progreso */}
                    <div className="aurela-progress-section">
                      <div className="aurela-progress-header">
                        <span className="aurela-progress-label">Progreso de Venta</span>
                        <span className="aurela-progress-percentage">
                          {Math.round(((rifa.elementos_vendidos || 0) / rifa.cantidad_elementos) * 100)}%
                        </span>
                      </div>
                      <div className="aurela-progress-bar">
                        <div 
                          className="aurela-progress-fill"
                          style={{
                            width: `${((rifa.elementos_vendidos || 0) / rifa.cantidad_elementos) * 100}%`
                          }}
                        ></div>
                      </div>
                    </div>

                    {/* Descripcin */}
                    {rifa.descripcion && (
                      <div className="aurela-description-section">
                        <p className="aurela-description">{rifa.descripcion}</p>
                      </div>
                    )}
                  </div>

                  {/* Botones de accin */}
                  <div className="aurela-card-actions">
                    <Link to={`/gestionar/${rifa.id}`} className="aurela-btn-primary">
                      <span className="aurela-btn-icon"></span>
                      <span className="aurela-btn-text">Gestionar</span>
                    </Link>
                    <Link to={`/public/${rifa.id}`} className="aurela-btn-secondary">
                      <span className="aurela-btn-icon"></span>
                      <span className="aurela-btn-text">Ver Pblico</span>
                    </Link>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      <div className="dashboard-footer">
        <Link to="/portal" className="btn-primary">Explorar Rifas</Link>
        <Link to="/gestionar" className="btn-secondary">Crear Rifa</Link>
      </div>
    </div>
  );
};

export default UserDashboard;
