import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

const LandingPage = () => {
  const { login, register, loading, error } = useAuth();
  const [showLogin, setShowLogin] = useState(false);
  const [showRegister, setShowRegister] = useState(false);
  const [loginData, setLoginData] = useState({ email: '', password: '' });
  const [registerData, setRegisterData] = useState({ 
    nombre: '', 
    email: '', 
    password: '', 
    confirmPassword: '' 
  });
  const [showParticipantRegister, setShowParticipantRegister] = useState(false);
  const [participantData, setParticipantData] = useState({
    nombre: '',
    email: '',
    telefono: ''
  });
  const [currentRifaIndex, setCurrentRifaIndex] = useState(0);

  // Rifas de ejemplo para mostrar en rotacin
  const rifasEjemplo = [
    {
      nombre: "iPhone 15 Pro Max",
      premio: "iPhone 15 Pro Max 256GB",
      disponibles: 47,
      dias: 12,
      precio: 50
    },
    {
      nombre: "TV Samsung 75\"",
      premio: "TV Samsung 75 pulgadas 4K",
      disponibles: 23,
      dias: 8,
      precio: 100
    },
    {
      nombre: "Laptop Gaming",
      premio: "Laptop ASUS ROG Strix G15",
      disponibles: 15,
      dias: 5,
      precio: 200
    },
    {
      nombre: "Consola PS5",
      premio: "PlayStation 5 + 2 juegos",
      disponibles: 31,
      dias: 18,
      precio: 75
    },
    {
      nombre: "Bicicleta Elctrica",
      premio: "Bicicleta elctrica Xiaomi",
      disponibles: 8,
      dias: 3,
      precio: 150
    }
  ];

  // Rotar rifas cada 4 segundos
  React.useEffect(() => {
    const interval = setInterval(() => {
      setCurrentRifaIndex((prevIndex) => 
        (prevIndex + 1) % rifasEjemplo.length
      );
    }, 4000);

    return () => clearInterval(interval);
  }, []);

  const handleLogin = async (e) => {
    e.preventDefault();
    const result = await login(loginData);
    if (result.success) {
      setShowLogin(false);
      setLoginData({ email: '', password: '' });
      // El contexto maneja la redireccin automticamente
    }
  };

  const handleRegister = async (e) => {
    e.preventDefault();
    if (registerData.password !== registerData.confirmPassword) {
      alert('Las contraseas no coinciden');
      return;
    }
    
    const result = await register({
      nombre: registerData.nombre,
      email: registerData.email,
      password: registerData.password,
      telefono: ''
    });
    
    if (result.success) {
      setShowRegister(false);
      setRegisterData({ nombre: '', email: '', password: '', confirmPassword: '' });
      // El contexto maneja la redireccin automticamente
    }
  };

  const handleParticipantRegister = async (e) => {
    e.preventDefault();
    try {
      // Crear usuario participante en el backend
      const response = await fetch('http://localhost:5001/api/participantes/registro', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(participantData)
      });

      if (response.ok) {
        alert('Registro exitoso! Ya puedes participar en las rifas.');
        setShowParticipantRegister(false);
        setParticipantData({ nombre: '', email: '', telefono: '' });
      } else {
        const errorData = await response.json();
        alert(errorData.error || 'Error al registrarse');
      }
    } catch (error) {
      console.error('Error registrando participante:', error);
      alert('Error al conectarse con el servidor');
    }
  };


  return (
    <div className="landing-page">
      {/* Hero Section */}
      <section className="hero-section">
        <div className="hero-content">
          <div className="hero-text">
            <h1 className="hero-title">
              <span className="hero-icon"></span>
              SorteoHub
            </h1>
            <p className="hero-subtitle">
              La plataforma profesional para crear rifas sin fines de lucro
            </p>
            <p className="hero-branding">
              made by <strong>AureLA Solutions</strong>
            </p>
            <p className="hero-description">
              Organiza rifas de manera segura, transparente y profesional. 
              Gestiona participantes, sorteos en vivo y pagos de forma fcil y confiable.
            </p>
            <div className="hero-buttons">
              <button 
                className="btn-hero-primary"
                onClick={() => setShowRegister(true)}
              >
                <span className="btn-icon"></span>
                <span>Crear Cuenta para Rifas</span>
              </button>
            </div>
            <div className="login-section">
              <p>Ya tienes cuenta para crear rifas?</p>
              <button 
                className="btn-hero-login"
                onClick={() => setShowLogin(true)}
              >
                <span className="btn-icon"></span>
                <span>Iniciar Sesin</span>
              </button>
            </div>

          </div>
          <div className="hero-image">
            <div className="hero-card">
              <div className="card-header">
                <span className="card-icon"></span>
                <span>Rifa Activa</span>
                <div className="card-indicators">
                  {rifasEjemplo.map((_, index) => (
                    <span 
                      key={index}
                      className={`indicator ${index === currentRifaIndex ? 'active' : ''}`}
                    />
                  ))}
                </div>
              </div>
              <div className="card-content">
                <h3>{rifasEjemplo[currentRifaIndex].nombre}</h3>
                <div className="card-stats">
                  <div className="stat">
                    <span className="stat-label">Disponibles</span>
                    <span className="stat-value available">{rifasEjemplo[currentRifaIndex].disponibles}</span>
                  </div>
                  <div className="stat">
                    <span className="stat-label">Das restantes</span>
                    <span className={`stat-value days ${rifasEjemplo[currentRifaIndex].dias <= 5 ? 'urgent' : rifasEjemplo[currentRifaIndex].dias <= 10 ? 'warning' : 'normal'}`}>
                      {rifasEjemplo[currentRifaIndex].dias}
                    </span>
                  </div>
                </div>
                <div className="card-prize">
                  <span className="prize-icon"></span>
                  <span>{rifasEjemplo[currentRifaIndex].premio}</span>
                </div>
                <div className="card-price">
                  <span className="price-label">Precio por boleto:</span>
                  <span className="price-value">${rifasEjemplo[currentRifaIndex].precio}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section className="features-section">
        <div className="container">
          <h2 className="section-title">
            <span className="section-icon"></span>
            Por qu elegir SorteoHub?
          </h2>
          <div className="features-grid">
            <div className="feature-card">
              <div className="feature-icon"></div>
              <h3>Seguridad Total</h3>
              <p>Transacciones seguras y datos protegidos. Cumplimos con todas las regulaciones de proteccin de datos.</p>
            </div>
            <div className="feature-card">
              <div className="feature-icon"></div>
              <h3>Fcil de Usar</h3>
              <p>Interfaz intuitiva que te permite crear y gestionar rifas en minutos, sin conocimientos tcnicos.</p>
            </div>
            <div className="feature-card">
              <div className="feature-icon"></div>
              <h3>Sorteos en Vivo</h3>
              <p>Realiza sorteos transparentes en vivo con testigos y transmisin en tiempo real.</p>
            </div>
            <div className="feature-card">
              <div className="feature-icon"></div>
              <h3>Gestin Completa</h3>
              <p>Controla participantes, pagos, nmeros vendidos y estadsticas en tiempo real.</p>
            </div>
            <div className="feature-card">
              <div className="feature-icon"></div>
              <h3>Personalizable</h3>
              <p>Diferentes tipos de rifas: nmeros, baraja, animales, colores, equipos deportivos y ms.</p>
            </div>
            <div className="feature-card">
              <div className="feature-icon"></div>
              <h3>Mltiples Pagos</h3>
              <p>Acepta transferencias bancarias con informacin CLABE y datos completos del titular.</p>
            </div>
          </div>
        </div>
      </section>

      {/* How It Works Section */}
      <section className="how-it-works-section">
        <div className="container">
          <h2 className="section-title">
            <span className="section-icon"></span>
            Cmo funciona?
          </h2>
          <div className="steps-grid">
            <div className="step-card">
              <div className="step-number">1</div>
              <div className="step-content">
                <h3>Regstrate</h3>
                <p>Crea tu cuenta gratuita en segundos y verifica tu identidad.</p>
              </div>
            </div>
            <div className="step-card">
              <div className="step-number">2</div>
              <div className="step-content">
                <h3>Crea tu Rifa</h3>
                <p>Configura premios, reglas, fechas y mtodos de pago de forma fcil.</p>
              </div>
            </div>
            <div className="step-card">
              <div className="step-number">3</div>
              <div className="step-content">
                <h3>Comparte</h3>
                <p>Enva el enlace a tus participantes y gestiona las ventas.</p>
              </div>
            </div>
            <div className="step-card">
              <div className="step-number">4</div>
              <div className="step-content">
                <h3>Realiza el Sorteo</h3>
                <p>Haz el sorteo en vivo con testigos y entrega los premios.</p>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="cta-section">
        <div className="container">
          <div className="cta-content">
            <h2>Listo para crear tu primera rifa?</h2>
            <p>nete a miles de organizadores que ya confan en SorteoHub</p>
            <button 
              className="btn-cta"
              onClick={() => setShowRegister(true)}
            >
              <span className="btn-icon"></span>
              <span>Empezar Ahora</span>
            </button>
          </div>
        </div>
      </section>

      {/* Participantes Section */}
      <section className="participantes-section">
        <div className="container">
          <div className="participantes-content">
            <h2> Quieres Participar en Rifas?</h2>
            <p>Regstrate como participante para acceder a todas las rifas disponibles, recibir notificaciones y ver tu historial personal</p>
            <div className="participantes-buttons">
              <button 
                className="btn-participant"
                onClick={() => setShowParticipantRegister(true)}
              >
                <span className="btn-icon"></span>
                <span>Registrarse como Participante</span>
              </button>
            </div>
          </div>
        </div>
      </section>

      {/* Login Modal */}
      {showLogin && (
        <div className="modal-overlay" onClick={() => setShowLogin(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>Iniciar Sesin</h3>
              <button 
                className="modal-close"
                onClick={() => setShowLogin(false)}
              >
                
              </button>
            </div>
            <form onSubmit={handleLogin} className="modal-form">
              {error && (
                <div style={{ color: '#ef4444', marginBottom: '1rem', padding: '0.5rem', backgroundColor: '#fef2f2', border: '1px solid #fecaca', borderRadius: '4px' }}>
                  {error}
                </div>
              )}
              <div className="form-group">
                <label>Email</label>
                <input
                  type="email"
                  value={loginData.email}
                  onChange={(e) => setLoginData({...loginData, email: e.target.value})}
                  required
                  disabled={loading}
                />
              </div>
              <div className="form-group">
                <label>Contrasea</label>
                <input
                  type="password"
                  value={loginData.password}
                  onChange={(e) => setLoginData({...loginData, password: e.target.value})}
                  required
                  disabled={loading}
                />
              </div>
              <button type="submit" className="btn-primary" disabled={loading}>
                <span className="btn-icon"></span>
                <span>{loading ? 'Iniciando sesin...' : 'Iniciar Sesin'}</span>
              </button>
            </form>
            <p className="modal-footer">
              No tienes cuenta? 
              <button 
                className="link-button"
                onClick={() => {
                  setShowLogin(false);
                  setShowRegister(true);
                }}
              >
                Regstrate aqu
              </button>
            </p>
          </div>
        </div>
      )}

      {/* Register Modal */}
      {showRegister && (
        <div className="modal-overlay" onClick={() => setShowRegister(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>Crear Cuenta</h3>
              <button 
                className="modal-close"
                onClick={() => setShowRegister(false)}
              >
                
              </button>
            </div>
            <form onSubmit={handleRegister} className="modal-form">
              <div className="form-group">
                <label>Nombre completo</label>
                <input
                  type="text"
                  value={registerData.nombre}
                  onChange={(e) => setRegisterData({...registerData, nombre: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Email</label>
                <input
                  type="email"
                  value={registerData.email}
                  onChange={(e) => setRegisterData({...registerData, email: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Contrasea</label>
                <input
                  type="password"
                  value={registerData.password}
                  onChange={(e) => setRegisterData({...registerData, password: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Confirmar contrasea</label>
                <input
                  type="password"
                  value={registerData.confirmPassword}
                  onChange={(e) => setRegisterData({...registerData, confirmPassword: e.target.value})}
                  required
                />
              </div>
              <button type="submit" className="btn-primary">
                <span className="btn-icon"></span>
                <span>Crear Cuenta</span>
              </button>
            </form>
            <p className="modal-footer">
              Ya tienes cuenta? 
              <button 
                className="link-button"
                onClick={() => {
                  setShowRegister(false);
                  setShowLogin(true);
                }}
              >
                Inicia sesin aqu
              </button>
            </p>
          </div>
        </div>
      )}

      {/* Participante Register Modal */}
      {showParticipantRegister && (
        <div className="modal-overlay" onClick={() => setShowParticipantRegister(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>Registro de Participante</h3>
              <button 
                className="modal-close"
                onClick={() => setShowParticipantRegister(false)}
              >
                
              </button>
            </div>
            <form onSubmit={handleParticipantRegister} className="modal-form">
              <div className="form-group">
                <input
                  type="text"
                  placeholder="Nombre completo"
                  value={participantData.nombre}
                  onChange={(e) => setParticipantData({...participantData, nombre: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <input
                  type="email"
                  placeholder="Email"
                  value={participantData.email}
                  onChange={(e) => setParticipantData({...participantData, email: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <input
                  type="tel"
                  placeholder="Telfono"
                  value={participantData.telefono}
                  onChange={(e) => setParticipantData({...participantData, telefono: e.target.value})}
                  required
                />
              </div>
              <div className="info-message">
                <p> Con tu registro podrs participar en rifas y recibir notificaciones de tus participaciones.</p>
              </div>
              <button type="submit" className="btn-primary">
                <span className="btn-icon"></span>
                <span>Registrarse</span>
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default LandingPage;
