import React, { createContext, useContext, useState, useEffect } from 'react';
import { rifasService, handleApiError } from '../services/api';
import { useAuth } from './AuthContext';

const RifasContext = createContext();

export const useRifas = () => {
  const context = useContext(RifasContext);
  if (!context) {
    throw new Error('useRifas debe ser usado dentro de un RifasProvider');
  }
  return context;
};

// Funcin helper para procesar rifas del backend
const procesarRifa = (rifa, isGuest = false) => {
  if (!rifa) return rifa;
  
  // Para invitados, no mostrar informacin sensible
  if (isGuest) {
    // Generar nmeros "vendidos" aleatorios para mostrar estado visual
    // sin revelar cules son realmente los vendidos
    const elementos = rifa.elementos_personalizados || [];
    const totalVendidos = parseInt(rifa.elementos_vendidos || 0);
    const totalReservados = parseInt(rifa.elementos_reservados || 0);
    
    // Crear una copia aleatoria de los elementos para ocultar los verdaderos vendidos
    const elementosAleatorios = [...elementos].sort(() => Math.random() - 0.5);
    const numerosVendidosSimulados = elementosAleatorios.slice(0, totalVendidos);
    const numerosReservadosSimulados = elementosAleatorios.slice(totalVendidos, totalVendidos + totalReservados);
    
    return {
      ...rifa,
      // Mostrar nmeros vendidos/reservados simulados para UI
      numerosVendidos: numerosVendidosSimulados,
      numerosReservados: numerosReservadosSimulados,
      participantes: [], // No mostrar participantes
      // Para compatibilidad con cdigo existente
      numerosDisponibles: elementos,
      // Preservar estadsticas agregadas del backend para mostrar en la UI
      elementos_vendidos: rifa.elementos_vendidos,
      elementos_reservados: rifa.elementos_reservados,
      elementos_disponibles: rifa.elementos_disponibles,
      total_participantes: rifa.total_participantes,
      total_recaudado: rifa.total_recaudado
    };
  }
  
  // Para usuarios autenticados, mostrar toda la informacin
  const participantes = rifa.participantes || [];
  const numerosVendidos = [];
  const numerosReservados = [];
  
  participantes.forEach(participante => {
    if (participante.estado === 'confirmado') {
      numerosVendidos.push(...participante.numeros_seleccionados);
    } else if (participante.estado === 'pendiente') {
      numerosReservados.push(...participante.numeros_seleccionados);
    }
  });
  
  return {
    ...rifa,
    numerosVendidos,
    numerosReservados,
    // Para compatibilidad con cdigo existente
    numerosDisponibles: rifa.elementos_personalizados || []
  };
};

export const RifasProvider = ({ children }) => {
  const [rifas, setRifas] = useState([]);
  const [myRifas, setMyRifas] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const { user, isAdmin } = useAuth();

  // Cargar rifas pblicas al montar el componente
  useEffect(() => {
    loadPublicRifas();
  }, []);

  // Cargar mis rifas si es admin
  useEffect(() => {
    if (isAdmin) {
      loadMyRifas();
    }
  }, [isAdmin]);

  const loadPublicRifas = async (filters = {}) => {
    try {
      setLoading(true);
      setError(null);

      const response = await rifasService.getPublicRifas(filters);
      const rifasProcesadas = (response.rifas || []).map(rifa => procesarRifa(rifa, !isAdmin));
      setRifas(rifasProcesadas);
    } catch (error) {
      console.error('Error cargando rifas pblicas:', error);
      setError('Error al cargar las rifas pblicas');
    } finally {
      setLoading(false);
    }
  };

  const loadMyRifas = async () => {
    try {
      setLoading(true);
      setError(null);

      const response = await rifasService.getMyRifas();
      console.log('RifasContext - Mis rifas cargadas:', response);
      const rifasProcesadas = (response.rifas || []).map(rifa => procesarRifa(rifa, false));
      setMyRifas(rifasProcesadas);
    } catch (error) {
      console.error('Error cargando mis rifas:', error);
      setError('Error al cargar tus rifas');
    } finally {
      setLoading(false);
    }
  };

  const getRifaById = async (id) => {
    try {
      setLoading(true);
      setError(null);

      const response = await rifasService.getRifaById(id);
      return procesarRifa(response.rifa, !isAdmin);
    } catch (error) {
      console.error('Error obteniendo rifa:', error);
      setError('Error al cargar la rifa');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  const createRifa = async (rifaData) => {
    try {
      setLoading(true);
      setError(null);

      const response = await rifasService.createRifa(rifaData);
      
      // Recargar mis rifas
      await loadMyRifas();
      
      return { success: true, rifa: response.rifa };
    } catch (error) {
      console.error('Error creando rifa:', error);
      const errorMessage = error.message || 'Error al crear la rifa';
      setError(errorMessage);
      return { success: false, error: errorMessage };
    } finally {
      setLoading(false);
    }
  };

  const updateRifa = async (id, rifaData) => {
    try {
      setLoading(true);
      setError(null);

      const response = await rifasService.updateRifa(id, rifaData);
      
      // Actualizar en mis rifas
      setMyRifas(prev => prev.map(rifa => 
        rifa.id === id ? { ...rifa, ...response.rifa } : rifa
      ));
      
      return { success: true, rifa: response.rifa };
    } catch (error) {
      console.error('Error actualizando rifa:', error);
      const errorMessage = error.message || 'Error al actualizar la rifa';
      setError(errorMessage);
      return { success: false, error: errorMessage };
    } finally {
      setLoading(false);
    }
  };

  const deleteRifa = async (id) => {
    try {
      setLoading(true);
      setError(null);

      await rifasService.deleteRifa(id);
      
      // Remover de mis rifas
      setMyRifas(prev => prev.filter(rifa => rifa.id !== id));
      
      return { success: true };
    } catch (error) {
      console.error('Error eliminando rifa:', error);
      const errorMessage = error.message || 'Error al eliminar la rifa';
      setError(errorMessage);
      return { success: false, error: errorMessage };
    } finally {
      setLoading(false);
    }
  };

  const clearError = () => {
    setError(null);
  };

  const value = {
    // Estado
    rifas,
    myRifas,
    loading,
    error,
    
    // Acciones
    loadPublicRifas,
    loadMyRifas,
    getRifaById,
    createRifa,
    updateRifa,
    deleteRifa,
    clearError,
    
    // Utilidades
    getRifasForUser: () => isAdmin ? myRifas : rifas,
  };

  return (
    <RifasContext.Provider value={value}>
      {children}
    </RifasContext.Provider>
  );
};
