import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';

const ParticipateRaffle = ({ rifas, setRifas }) => {
  const { id } = useParams();
  const [rifa, setRifa] = useState(null);
  const [participante, setParticipante] = useState({
    nombre: '',
    telefono: '',
    email: '',
    numerosSeleccionados: []
  });
  // Registro es obligatorio ahora
  const [numerosDisponibles, setNumerosDisponibles] = useState([]);
  const [mostrarPago, setMostrarPago] = useState(false);
  const [reservaId, setReservaId] = useState(null);

  useEffect(() => {
    if (id) {
      const rifaEncontrada = rifas.find(r => r.id === id);
      if (rifaEncontrada) {
        // Asegurar que la rifa tenga todas las propiedades necesarias
        const rifaCompleta = {
          ...rifaEncontrada,
          numerosReservados: rifaEncontrada.numerosReservados || [],
          numerosVendidos: rifaEncontrada.numerosVendidos || []
        };
        setRifa(rifaCompleta);
        
        // Mostrar TODOS los nmeros de la rifa
        setNumerosDisponibles(rifaCompleta.elementos_personalizados || []);
      }
    }
  }, [id, rifas]);

  const seleccionarNumero = (numero) => {
    // Verificar si el nmero est disponible (no vendidos ni reservados)
    const esDisponible = !(rifa.numerosVendidos || []).includes(numero) && 
                        !(rifa.numerosReservados || []).includes(numero);
    
    if (!esDisponible) return; // No permitir seleccionar nmeros no disponibles
    
    if (participante.numerosSeleccionados.includes(numero)) {
      setParticipante({
        ...participante,
        numerosSeleccionados: participante.numerosSeleccionados.filter(n => n !== numero)
      });
    } else {
      setParticipante({
        ...participante,
        numerosSeleccionados: [...participante.numerosSeleccionados, numero]
      });
    }
  };

  const continuarPago = () => {
    // Validar que haya nmeros seleccionados
    if (participante.numerosSeleccionados.length === 0) {
      alert('Por favor, selecciona al menos un nmero para continuar');
      return;
    }
    
    // Mostrar la seccin de pago con los campos de datos
    setMostrarPago(true);
    
    // Scroll automtico al top para mostrar los campos de datos
    setTimeout(() => {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }, 100);
  };

  const liberarReserva = () => {
    // Funcin simplificada - no necesitamos liberar reservas locales
    setReservaId(null);
  };

  const confirmarParticipacion = async () => {
    // Validar que se hayan llenado los campos requeridos
    if (!participante.nombre.trim()) {
      alert('Por favor, ingresa tu nombre completo');
      return;
    }
    
    if (!participante.telefono.trim()) {
      alert('Por favor, ingresa tu nmero de telfono');
      return;
    }
    
    if (!participante.email.trim()) {
      alert('Por favor, ingresa tu email');
      return;
    }
    
    // Validar formato de email bsico
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(participante.email)) {
      alert('Por favor, ingresa un email vlido');
      return;
    }

    try {
      // Enviar participacin al backend
      const response = await fetch(`http://localhost:5001/api/participantes/${rifa.id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          nombre: participante.nombre.trim(),
          telefono: participante.telefono.trim(),
          email: participante.email.trim(),
          numerosSeleccionados: participante.numerosSeleccionados.map(num => String(num)),
          quiereRegistro: true // Registro obligatorio
        })
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Error al registrar participacin');
      }

      const result = await response.json();
      
      // Mostrar mensaje de confirmacin
      alert(`Participacin registrada exitosamente! Los nmeros ${participante.numerosSeleccionados.join(', ')} han sido reservados. El organizador validar tu pago y te notificar por email.`);
      
      // Limpiar formulario y volver a seleccin
      setParticipante({
        nombre: '',
        telefono: '',
        email: '',
        numerosSeleccionados: []
      });
      setMostrarPago(false);
      setReservaId(null);
      
    } catch (error) {
      console.error('Error al confirmar participacin:', error);
      alert(`Error al registrar participacin: ${error.message}`);
    }
  };

  if (!rifa) {
    return (
      <div className="participate-container">
        <div className="error-message">
          <h2>Rifa no encontrada</h2>
          <p>La rifa que buscas no existe o ha sido eliminada.</p>
          <Link to="/portal" className="btn-primary">Volver al Portal</Link>
        </div>
      </div>
    );
  }

  if (!rifa.activa) {
    return (
      <div className="participate-container">
        <div className="header-top">
          <Link to="/" className="btn-back-to-rifas">
             Regresar a las Rifas
          </Link>
        </div>
        <div className="error-message">
          <h2>Rifa Finalizada</h2>
          <p>Esta rifa ya ha finalizado y no acepta ms participantes.</p>
          <Link to="/" className="btn-primary">Ver Otras Rifas</Link>
        </div>
      </div>
    );
  }

  const totalPagar = rifa.precio * participante.numerosSeleccionados.length;

  return (
    <div className="participate-container">
      <div className="participate-header">
        <div className="header-top">
          <Link to="/" className="btn-back-to-rifas">
             Regresar a las Rifas
          </Link>
        </div>
        <h1> Participar en: {rifa.nombre}</h1>
        <Link to={`/public/${rifa.id}`} className="btn-secondary"> Ver Detalles</Link>
      </div>

      <div className="participate-content">
        {!mostrarPago ? (
          <>
            {/* Informacin de la rifa */}
            <div className="rifa-info-participate">
              <div className="info-card">
                <h3> Informacin</h3>
                <div className="info-grid">
                  <div className="info-item">
                    <span className="info-label">Precio por elemento:</span>
                    <span className="info-value">${rifa.precio}</span>
                  </div>
                  <div className="info-item">
                    <span className="info-label">Disponibles:</span>
                    <span className="info-value">{rifa.numerosDisponibles.filter(n => 
                      !rifa.numerosVendidos.includes(n) && 
                      !(rifa.numerosReservados || []).includes(n) &&
                      !participante.numerosSeleccionados.includes(n)
                    ).length}</span>
                  </div>
                  <div className="info-item">
                    <span className="info-label">Vendidos:</span>
                    <span className="info-value">{rifa.elementos_vendidos || 0}</span>
                  </div>
                  {rifa.fecha_fin && (
                    <div className="info-item">
                      <span className="info-label">Finaliza:</span>
                      <span className="info-value">{new Date(rifa.fecha_fin).toLocaleDateString()}</span>
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* Seleccin de nmeros */}
            <div className="numeros-seleccion-participate">
              <h3> Selecciona tus Nmeros</h3>
            
              <div className="numeros-grid-participate">
                {numerosDisponibles.map(numero => {
                  const vendido = (rifa.numerosVendidos || []).includes(numero);
                  const reservado = (rifa.numerosReservados || []).includes(numero);
                  const disponible = !vendido && !reservado;
                  const seleccionado = participante.numerosSeleccionados.includes(numero);
                  
                  let estado = 'disponible';
                  if (seleccionado) {
                    estado = 'seleccionado';
                  } else if (vendido) {
                    estado = 'vendido';
                  } else if (reservado) {
                    estado = 'reservado';
                  }
                  
                  return (
                    <button
                      key={numero}
                      className={`numero-participate-btn ${estado}`}
                      onClick={() => seleccionarNumero(numero)}
                      disabled={!disponible}
                      title={vendido ? 'Vendido' : reservado ? 'Reservado' : 'Disponible'}
                    >
                      {numero}
                    </button>
                  );
                })}
              </div>
            </div>

            <button 
              onClick={continuarPago} 
              className="btn-primary btn-continuar"
              disabled={participante.numerosSeleccionados.length === 0}
            >
              Continuar al Pago (${totalPagar})
            </button>
          </>
        ) : (
          <>
            {/* Datos del participante */}
            <div className="participante-form">
              <div className="form-header">
                <h3> Tus Datos</h3>
                <p className="form-description">Completa tus datos para participar en la rifa:</p>
              </div>
              
              <div className="form-layout">
                <div className="form-fields">
                  <div className="form-group">
                    <label htmlFor="nombre">Nombre completo *</label>
                    <input
                      id="nombre"
                      type="text"
                      placeholder="Ingresa tu nombre completo"
                      value={participante.nombre}
                      onChange={(e) => setParticipante({...participante, nombre: e.target.value})}
                      required
                    />
                  </div>
                  
                  <div className="form-group">
                    <label htmlFor="telefono">Telfono *</label>
                    <input
                      id="telefono"
                      type="tel"
                      placeholder="Ingresa tu nmero de telfono"
                      value={participante.telefono}
                      onChange={(e) => setParticipante({...participante, telefono: e.target.value})}
                      required
                    />
                  </div>
                  
                  <div className="form-group">
                    <label htmlFor="email">Email *</label>
                    <input
                      id="email"
                      type="email"
                      placeholder="Ingresa tu email"
                      value={participante.email}
                      onChange={(e) => setParticipante({...participante, email: e.target.value})}
                      required
                    />
                  </div>
                </div>
                
                <div className="info-message">
                  <div className="info-icon"></div>
                  <div className="info-text">
                    <p><strong>Importante:</strong> Con tu email podrs recibir notificaciones y ver tu historial de participaciones.</p>
                  </div>
                </div>
              </div>
            </div>

            {/* Informacin de pago */}
            <div className="pago-info">
              <h3> Informacin de Pago</h3>
              
              <div className="resumen-pago">
                <div className="resumen-header">
                  <h4> Resumen de tu participacin</h4>
                </div>
                <div className="resumen-content">
                  <div className="resumen-item">
                    <span className="resumen-label">Nmeros seleccionados:</span>
                    <span className="resumen-value">{participante.numerosSeleccionados.join(', ')}</span>
                  </div>
                  <div className="resumen-item">
                    <span className="resumen-label">Precio por nmero:</span>
                    <span className="resumen-value">${rifa.precio}</span>
                  </div>
                  <div className="resumen-item">
                    <span className="resumen-label">Cantidad de nmeros:</span>
                    <span className="resumen-value">{participante.numerosSeleccionados.length}</span>
                  </div>
                  <div className="resumen-item total">
                    <span className="resumen-label">Total a pagar:</span>
                    <span className="resumen-value">${totalPagar}</span>
                  </div>
                </div>
              </div>

              {rifa.formasPago && rifa.formasPago.transferencia && (
                <div className="datos-pago">
                  <h4> Datos para Transferencia:</h4>
                  <div className="pago-details">
                    <p><strong>Banco:</strong> {rifa.formasPago.banco}</p>
                    <p><strong>CLABE:</strong> {rifa.formasPago.clabe}</p>
                    <p><strong>Nmero de cuenta:</strong> {rifa.formasPago.numeroCuenta}</p>
                    <p><strong>Titular:</strong> {rifa.formasPago.nombreTitular}</p>
                    {rifa.formasPago.telefono && (
                      <p><strong>Telfono:</strong> {rifa.formasPago.telefono}</p>
                    )}
                    {rifa.formasPago.whatsapp && (
                      <p><strong>WhatsApp:</strong> {rifa.formasPago.whatsapp}</p>
                    )}
                  </div>
                </div>
              )}

              <div className="instrucciones-pago">
                <h4> Instrucciones:</h4>
                <ol>
                  <li>Realiza la transferencia por el monto exacto: <strong>${totalPagar}</strong></li>
                  <li>Guarda el comprobante de pago</li>
                  <li>Confirma tu participacin haciendo clic en "Confirmar Participacin"</li>
                  <li>El organizador verificar tu pago y confirmar tus nmeros</li>
                </ol>
              </div>

              <div className="pago-actions">
                <button 
                  onClick={() => {
                    liberarReserva();
                    setMostrarPago(false);
                  }} 
                  className="btn-secondary btn-cancelar"
                >
                   Cancelar Reserva
                </button>
                <button 
                  onClick={confirmarParticipacion} 
                  className="btn-primary btn-confirmar"
                  disabled={!participante.nombre.trim() || !participante.telefono.trim() || !participante.email.trim()}
                >
                   Confirmar Participacin
                </button>
              </div>
              
              <div className="reserva-info">
                <div className="reserva-warning">
                  <div className="warning-icon"></div>
                  <div className="warning-text">
                    <p><strong>Reserva temporal:</strong> Tienes 15 minutos para completar el pago. Los nmeros se liberarn automticamente si no confirmas.</p>
                  </div>
                </div>
                <div className="reserva-numbers">
                  <div className="numbers-icon"></div>
                  <div className="numbers-text">
                    <p><strong>Nmeros reservados:</strong> {participante.numerosSeleccionados.join(', ')}</p>
                  </div>
                </div>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default ParticipateRaffle;
